import { useState } from "react";
import { Edit } from "lucide-react";
import Switch from "@/components/ui/switch";
import { NorthEast, SouthWest } from '@mui/icons-material';
import { toast } from "sonner";
import { ConfirmationAlert } from "@/components/ui/ConfirmationAlert";
import { ClipboardCopy } from "lucide-react";


interface TableColumn {
  key: string;
  id: string;
  name: string;
  cell?: (value: any, row: any) => React.ReactNode;
}

interface HandleStatusChange {
  (row: any, checked: boolean): void;
}

interface HandleNavigate {
  (path: string, rowData: string): void;
}

export const getUserTableColumns = (
  handleStatusChange: HandleStatusChange,
  handleNavigate: HandleNavigate
): TableColumn[] => {
  return [
    {
      key: "first_name",
      id: "first_name",
      name: "First Name",
    },
    {
      key: "last_name",
      id: "last_name",
      name: "Last Name",
    },
    {
      key: "email",
      id: "email",
      name: "Email",
    },
    {
      key: "phone_number",
      id: "phone_number",
      name: "Phone Number",
      cell: (_, row: any) => {
        const countryCode = row.country_code || "";
        const fullNumber = row.phone_number || "";
        const localNumber = fullNumber.startsWith(countryCode)
          ? fullNumber.slice(countryCode.length)
          : fullNumber;
        return <span>{localNumber}</span>;
      },
    },
    {
      key: "role_id",
      id: "role_id",
      name: "Role", // optionally map this to a role name
      cell: (_, row: any) => (
        <span>{row.role_id == 1 ? 'Client' : 'Admin'}</span>
      ),
    },
    {
      key: "date_joined",
      id: "date_joined",
      name: "Started On",
      cell: (_, row: any) => {
        return row?.date_joined
          ? new Date(row.date_joined).toLocaleDateString()
          : "-";
      },
    },
    {
      key: "is_verified",
      id: "is_verified",
      name: "Verified",
      cell: (_, row: any) => (
        <span>{row.is_verified ? "Yes" : "No"}</span>
      ),
    },
    {
      key: "status",
      id: "status",
      name: "Status",
      cell: (_, row: any) => {
        return (
          <div className="flex justify-center">
            <Switch
              checked={row?.is_active}
              onCheckedChange={(checked) => {
                handleStatusChange(row, checked);
              }}
            />
          </div>
        );
      },
    },
    {
      key: "actions",
      id: "actions",
      name: "Action",
      cell: (_, rowData: any) => {
        return (
          <div className="flex justify-center">
            <button
              className="p-2 rounded-md hover:bg-muted/20"
              onClick={() => {
                handleNavigate(`update-user/${rowData.id}`, rowData);
              }}
            >
              <Edit className="w-5 h-5 text-muted-foreground" />
            </button>
          </div>
        );
      },
    },
    {
      id: "expand",
      name: "expand",
      renderDetailPanel: (row) => (
        <div className="p-4">
          <h3>Details for {row.name}</h3>
          <p>Additional info: {row.description || "No description"}</p>
        </div>
      ),
    },
  ];
};
